/**
Global
*/
* {
    /*border: 1px solid black;*/
    /*box-sizing: border-box;
    font-family: monospace;*/
}

h1, h2, h3, h4, p {
    margin: 0;
    padding: 5px 10px;

}

.h3 {
    font-size: 1.33em;
}

.full {
    width: 100%;
    height: 100%;
}
/**
About _flex
*/
.flex {
    display: -ms-flexbox;
    display: flex;
}

.flex-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}


.flex-column {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.flex1 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
}

.flex-row-reverse {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
}

